/* components/OptionsList.css */

.options-list {
    height: 100vh;
    background-color: #f8f9fa;
    border-right: 0.05vw solid #dee2e6;
    position: relative;
    overflow-y: auto; /* Добавляем прокрутку, если контент не помещается */
}

.options-list h3 {
    padding: 1vh 1vw;
    margin: 0;
    font-size: 1vw;
    color: #343a40;
    border-bottom: 0.05vw solid #dee2e6;
}

.tree-view {
    list-style: none;
    padding: 0;
    margin: 0;
}

.tree-item {
    padding: 0.5vh 0;
}

.tree-item-header {
    padding: 0.5vh 1vw;
    cursor: pointer;
    display: flex;
    align-items: center;
    font-size: 0.875vw;
    color: #343a40;
}

.tree-toggle {
    margin-right: 0.5vw;
    font-size: 0.75vw;
}

.tree-children {
    list-style: none;
    padding-left: 2vw;
    margin: 0;
}

.tree-child-item {
    padding: 0.4vh 0.8vw;
    background-color: #fff;
    border: 0.05vw solid #dee2e6;
    border-radius: 0.2vw;
    font-size: 0.75vw; /* Уменьшаем шрифт для компактности */
    color: #343a40;
    transition: background-color 0.2s ease;
    cursor: pointer;
    margin: 0.2vh 0; /* Уменьшаем вертикальный отступ */
    white-space: nowrap; /* Предотвращаем перенос текста */
    overflow: hidden;
    text-overflow: ellipsis; /* Обрезаем длинный текст с многоточием */
}

.tree-child-item:hover {
    background-color: #e9ecef;
}

.tree-child-item.selected {
    background-color: #cce5ff;
    color: #007bff;
    font-weight: bold;
}

.resize-handle {
    position: absolute;
    top: 0;
    right: 0;
    width: 0.3vw;
    height: 100%;
    background-color: #dee2e6;
    cursor: ew-resize;
}

.table-actions {
    padding: 0.5vh 0; /* Уменьшаем внутренние отступы */
}

.action-buttons {
    display: flex;
    flex-wrap: wrap; /* Разрешаем перенос кнопок на следующую строку */
    gap: 0.3vw; /* Уменьшаем расстояние между кнопками */
    justify-content: flex-start; /* Выравниваем кнопки по левому краю */
}

.add-table-btn {
    background-color: #4CAF50;
    color: white;
    border: none;
    padding: 0.3vh 0.5vw; /* Уменьшаем padding */
    font-size: 0.75vw; /* Уменьшаем шрифт */
    transition: background-color 0.2s ease;
}

.add-table-btn:hover {
    background-color: #45a049;
}

.drop-table-btn {
    background-color: #dc3545;
    color: white;
    border: none;
    padding: 0.3vh 0.5vw;
    font-size: 0.75vw;
    transition: background-color 0.2s ease;
}

.drop-table-btn:hover {
    background-color: #c82333;
}

.backup-btn {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 0.3vh 0.5vw;
    font-size: 0.75vw;
    transition: background-color 0.2s ease;
}

.backup-btn:hover {
    background-color: #0056b3;
}

.restore-btn {
    background-color: #007bff;
    color: black;
    border: none;
    padding: 0.3vh 0.5vw;
    font-size: 0.75vw;
    transition: background-color 0.2s ease;
}

.restore-btn:hover {
    background-color: #0056b3;
}